import { useCallback, useContext } from 'react'

import Icon from '../../Common/Icon'
import { FileSystemContext } from './store'

export default function ImportKeymapsButton () {
  const [, { openImportModal, selectFilesForImport }] = useContext(FileSystemContext)
  const handleClick = useCallback(async () => {
    const handles = await window.showOpenFilePicker({
      mode: 'readwrite',
      multiple: true,
      excludeAcceptAllOption: true,
      types: [{
        accept: {
          'text/plain': ['.keymap']
        }
      }]
    })

    selectFilesForImport({ handles })
    openImportModal()
  }, [openImportModal, selectFilesForImport])

  return (
    <button onClick={handleClick}>
      <Icon name="file-import" /> Import Keymaps
    </button>
  )
}
