import cloneDeep from 'lodash/cloneDeep'
import { useContext, useEffect } from 'react'

import FileSystemKeymapPicker from './FileSystemKeymapPicker'
import ImportKeymapsButton from './ImportKeymapsButton'
import ImportKeymapsDialog from './ImportKeymapsDialog'
import ExportKeymapButton from './ExportKeymapButton'
import { loadHandles, FileSystemContext, withFileSystemStore, loadActiveHandle } from './store'
import FeedbackModalButton from './FeedbackModalButton'
import FeedbackDialog from './FeedbackDialog'

function FileSystemPicker ({ onSelect }) {
  const [{
    keymapHandles,
    activeKeymap,
    importModalOpen,
    feedbackModalOpen
  }, { initHandles }] = useContext(FileSystemContext)

  useEffect(() => {
    ;(async () => {
      const handles = await loadHandles()
      const activeHandle = await loadActiveHandle()

      initHandles({ handles, activeHandle })
    })()
  }, [initHandles])

  useEffect(() => {
    if (!activeKeymap.keymapParsed) {
      return
    }

    const { keymapParsed: keymap, metadataParsed: metadata } = activeKeymap
    const { layouts } = metadata
    const preferredLayout = keymap.chosen['zmk,matrix_transform']
      ? keymap.chosen['zmk,matrix_transform'].replace(/^&/, '')
      : null

    const defaultLayout = Object.keys(layouts)[0]
    const layoutMetadata = (
      layouts[preferredLayout] ||
      layouts[defaultLayout]
    )

    onSelect(cloneDeep({
      source: 'filesystem',
      keyboard: metadata.id,
      layoutMetadata,
      layout: layoutMetadata.layout,
      keymap,
      sensors: layoutMetadata.sensors || []
    }))
  }, [activeKeymap, onSelect])

  return (
    <>
      {keymapHandles.length > 0
        ? <FileSystemKeymapPicker />
        : <ImportKeymapsButton />}
      {activeKeymap?.keymapParsed && <ExportKeymapButton />}
      {importModalOpen && <ImportKeymapsDialog />}
      <FeedbackModalButton />
      {feedbackModalOpen && <FeedbackDialog />}
    </>
  )
}

export default withFileSystemStore(FileSystemPicker)
