import { useCallback, useContext, useEffect, useMemo } from 'react'

import { Select } from '../Controls'
import ImportKeymapsButton from './ImportKeymapsButton'
import { FileSystemContext, saveActiveHandle } from './store'
import extractKeymapData from 'keymap-editor-zmk-backend/lib/extract-keymap-data'
import { stripLegacyProperties } from '../../keymap'

const fileOptions = { mode: 'read' }

export default function FileSystemKeymapPicker () {
  const [{ keymapHandles, selectedHandleIndex }, { selectHandle, selectKeymap }] = useContext(FileSystemContext)
  const choices = useMemo(() => keymapHandles.map((handle, i) => ({
    id: i, name: handle.name
  })), [keymapHandles])

  const handleSelect = useCallback(async choice => {
    await saveActiveHandle(choice.id)
    selectHandle({ index: choice.id })
  }, [selectHandle])

  useEffect(() => {
    const index = selectedHandleIndex
    const handle = keymapHandles[index]

    if (!handle) {
      return
    }

    ;(async function () {
      if (
        await handle.queryPermission(fileOptions) !== 'granted' &&
        await handle.requestPermission(fileOptions) !== 'granted'
      ) {
        return
      }

      const name = handle.name.replace('.keymap', '')
      const file = await handle.getFile()
      const text = await file.text()
      const parsed = await extractKeymapData(text)
      const metadata = await import(
        /* webpackInclude: /\.json$/ */
        /* webpackMode: "lazy" */
        /* webpackChunkName: "keyboard-data/[request]-metadata" */
        `keymap-editor-contrib/keyboard-data/${name}.json`
      )

      stripLegacyProperties(parsed)
      selectKeymap({
        handleIndex: index,
        keymapRaw: text,
        keymapParsed: parsed,
        metadataParsed: metadata
      })
    })()
  }, [keymapHandles, selectedHandleIndex, selectKeymap])

  const value = useMemo(
    () => choices[selectedHandleIndex],
    [choices, selectedHandleIndex]
  )

  return (
    <Select
      value={value}
      options={choices}
      onChange={handleSelect}
      getOptionLabel={option => option.name}
      getOptionValue={option => option.id}
    >
      <ImportKeymapsButton />
    </Select>
  )
}
