import Icon from '../../Common/Icon'
import useKeymapUpdater from './updater'

export default function ExportKeymapButton () {
  const [updateNeeded, update] = useKeymapUpdater()
  return (
    <button
      disabled={!updateNeeded}
      title={updateNeeded ? 'Save changes' : 'No changes'}
      onClick={update}
    >
      <Icon name="file-export" />
    </button>
  )
}
