import { useCallback, useContext, useMemo } from 'react'

import keyboardCatalog from 'keymap-editor-contrib/keyboard-catalog.json'

import ActionButton from '../../Common/ActionButton'
import DialogBox from '../../Common/DialogBox'
import Icon from '../../Common/Icon'
import Modal from '../../Common/Modal'
import { FileSystemContext, saveHandles } from './store'

export default function ImportKeymapsDialog () {
  const [{ pendingImports }, { closeImportModal, updatedHandles }] = useContext(FileSystemContext)
  const acceptedImports = useMemo(() => pendingImports.filter(handle => keymapInCatalog(handle.name)), [pendingImports])
  const rejectedImports = useMemo(() => pendingImports.filter(handle => !keymapInCatalog(handle.name)), [pendingImports])

  const handleConfirm = useCallback(async () => {
    await saveHandles(acceptedImports)
    saveHandles(acceptedImports)
    updatedHandles({ handles: acceptedImports })
    closeImportModal()
  }, [acceptedImports, updatedHandles, closeImportModal])

  const handleCancel = useCallback(() => {
    closeImportModal()
  }, [closeImportModal])

  return (
    <Modal>
      <DialogBox dismissText={null}>
        <h2>Importing Keymaps</h2>
        <h3>Keymap files ready to be imported</h3>
        <ul>
          {pendingImports.map((handle, i) => (
            <li key={i}><Handle value={handle} /></li>
          ))}
        </ul>
        {rejectedImports.length > 0 && (
          <p>
            <em>
              Some keymap files could not be matched with known keyboards and
              will not be imported. This is a work in progress and in the future
              you will have the option to import your own metadata file.
            </em>
          </p>
        )}
        <div style={{ textAlign: 'center' }}>
          <ActionButton onClick={handleConfirm}>Continue</ActionButton>
          <ActionButton onClick={handleCancel} secondary>Cancel</ActionButton>
        </div>
      </DialogBox>
    </Modal>
  )
}

function keymapInCatalog (name) {
  return name.replace('.keymap', '') in keyboardCatalog
}

function Handle ({ value }) {
  return (
    <>
      {keymapInCatalog(value.name)
        ? <Icon name="check" style={{ color: 'mediumseagreen' }} />
        : <Icon name="question" style={{ color: 'firebrick' }} />
      } {value.name}
    </>
  )
}
