import { get as idbGet, set as idbSet } from 'idb-keyval'
import { createContext, useMemo, useReducer } from 'react'
import { createSlice } from '@reduxjs/toolkit'

const STORAGE_KEY_HANDLES = 'KEYMAP_EDITOR_IDB_FS_KEYMAP_HANDLES'
const STORAGE_KEY_SELECTED_HANDLE_INDEX = 'KEYMAP_EDITOR_IDB_FS_KEYMAP_SELECTED_HANDLE_INDEX'

// Using thunks would be better but because I'm not putting this slice into a
// proper redux store I don't actually have the thunk middleware loaded
// TODO: is adding the middleware manually feasible?
// TODO: is having multiple independent stores possible?
export const loadHandles = () => idbGet(STORAGE_KEY_HANDLES)
export const saveHandles = handles => idbSet(STORAGE_KEY_HANDLES, handles)
export const loadActiveHandle = () => idbGet(STORAGE_KEY_SELECTED_HANDLE_INDEX)
export const saveActiveHandle = index => idbSet(STORAGE_KEY_SELECTED_HANDLE_INDEX, index)

const initialState = () => ({
  keymapHandles: [],
  selectedHandleIndex: null,
  activeKeymap: {
    handleIndex: null,
    keymapRaw: null,
    keymapParsed: null,
    metadataParsed: null
  },
  importModalOpen: false,
  feedbackModalOpen: false,
  pendingImports: null
})

const slice = createSlice({
  name: 'filesystem-picker',
  initialState,
  reducers: {
    openImportModal (state) { state.importModalOpen = true },
    closeImportModal (state) { state.importModalOpen = false },
    openFeedbackModal (state) { state.feedbackModalOpen = true },
    closeFeedbackModal (state) { state.feedbackModalOpen = false },
    selectFilesForImport (state, action) {
      const { handles } = action.payload
      state.pendingImports = handles
    },
    updatedHandles (state, action) {
      state.keymapHandles = action.payload.handles
    },
    initHandles (state, action) {
      const { handles = [] } = action.payload
      // No point in trying to initialize the active handle based on a previous
      // session, as this is likely happening during the initial load before any
      // user interaction and therefore we can't request user permissions.
      state.keymapHandles = handles
    },
    selectHandle (state, action) {
      state.selectedHandleIndex = action.payload.index
    },
    selectKeymap (state, action) {
      const { handleIndex, keymapRaw, keymapParsed, metadataParsed } = action.payload
      state.activeKeymap = {
        handleIndex,
        keymapRaw,
        keymapParsed,
        metadataParsed
      }
    },
    updateActiveKeymap (state, action) {
      const { keymapParsed, keymapRaw } = action.payload
      state.activeKeymap.keymapParsed = keymapParsed
      state.activeKeymap.keymapRaw = keymapRaw
    }
  }
})

function useActionDispatchers (dispatch) {
  return useMemo(() => (
    Object.keys(slice.actions).reduce((actions, action) => {
      actions[action] = (...args) => dispatch(slice.actions[action](...args))
      return actions
    }, {})
  ), [dispatch])
}

export function useFileSystemStore () {
  const [state, dispatch] = useReducer(slice.reducer, null, initialState)
  const dispatchers = useActionDispatchers(dispatch)

  return [state, dispatchers]
}

export const FileSystemContext = createContext([{}, {}])

export function FileSystemStore ({ children }) {
  const [state, dispatchers] = useFileSystemStore()

  return (
    <FileSystemContext.Provider value={[state, dispatchers]}>
      {children}
    </FileSystemContext.Provider>
  )
}

export function withFileSystemStore (Component) {
  return props => (
    <FileSystemStore>
      <Component {...props} />
    </FileSystemStore>
  )
}
